'use client';

import { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';

type Props = {
  mode?: 'create' | 'edit';
  userId?: string;
  initialData?: any; // optional preloaded data: {firstName, lastName, username, email, image, ...}
  onSuccess?: () => void;
  onCancel?: () => void;
  onDelete?: (userId: string) => void;
  forceChangePassword?: boolean;
  forceChangeUsername?: boolean;
};

function getCameFrom() {
  if (typeof window !== 'undefined') {
    return sessionStorage.getItem('came-from') || '/';
  }
  return '/';
}

export default function CreateOrEditUserForm({
  mode = 'create',
  userId,
  initialData,
  onSuccess,
  onCancel,
  onDelete,
  forceChangePassword = false,
  forceChangeUsername = false,
}: Props) {
  // Defensive: don't even render the form if edit mode and userId is missing
  if (mode === 'edit' && !userId && !initialData) {
    return <div className="text-red-500 text-center">User ID missing. Cannot edit user</div>;
  }

  // initialRef keeps a snapshot of the data we consider "original" for diffing
  const initialRef = useRef<any>(initialData ?? null);
  useEffect(() => {
    // keep ref in sync if parent passed new initialData
    if (initialData) initialRef.current = initialData;
  }, [initialData]);

  const isEdit = mode === 'edit' || !!userId || !!initialData;

  // Form state (visible values)
  const [form, setForm] = useState({
    firstName: initialData?.firstName ?? '',
    lastName: initialData?.lastName ?? '',
    username: initialData?.username ?? '',
    email: initialData?.email ?? '',
    password: '',
    confirmPassword: '',
  });
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imageUrl, setImageUrl] = useState<string>(initialData?.image || '');
  const [imagePreview, setImagePreview] = useState<string | null>(initialData?.image || null);
  const [imageToDelete, setImageToDelete] = useState<string | null>(null);

  const [changeUsername, setChangeUsername] = useState(false);
  const [newUsername, setNewUsername] = useState('');
  const [confirmNewUsername, setConfirmNewUsername] = useState('');

  const [changePassword, setChangePassword] = useState(false);
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');

  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [checkingUsername, setCheckingUsername] = useState(false);
  const [usernameInvalid, setUsernameInvalid] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [registerDot, setRegisterDot] = useState(0);

  const [loadingUser, setLoadingUser] = useState(false);

  const router = useRouter();

  // If in edit mode and we have a userId, always fetch the latest user data to populate the form.
  // This ensures the form fields show existing data when "Edit" is clicked (even if parent didn't pass initialData).
  useEffect(() => {
    let aborted = false;
    const shouldFetch = isEdit && userId;
    if (!shouldFetch) return;

    async function fetchUser() {
      setLoadingUser(true);
      setError('');
      try {
        const res = await fetch(`/api/users/${encodeURIComponent(String(userId))}`);
        if (!res.ok) {
          const txt = await res.text().catch(() => '');
          throw new Error(txt || `Failed to load user (${res.status})`);
        }
        const json = await res.json();
        if (aborted) return;

        // Populate form fields from fetched user
        initialRef.current = json;
        setForm({
          firstName: json?.firstName ?? '',
          lastName: json?.lastName ?? '',
          username: json?.username ?? '',
          email: json?.email ?? '',
          password: '',
          confirmPassword: '',
        });
        setImageUrl(json?.image ?? '');
        setImagePreview(json?.image ?? null);

        // Reset change toggles according to forced props (or false)
        setChangePassword(Boolean(forceChangePassword));
        setChangeUsername(Boolean(forceChangeUsername));
        setNewPassword('');
        setConfirmNewPassword('');
        setNewUsername('');
        setConfirmNewUsername('');
        setUsernameInvalid(false);
        setError('');
        setSuccess('');
      } catch (err: any) {
        if (!aborted) setError(err?.message || 'Failed to load user');
      } finally {
        if (!aborted) setLoadingUser(false);
      }
    }

    fetchUser();
    return () => {
      aborted = true;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, userId, forceChangePassword, forceChangeUsername]);

  // If parent provides initialData explicitly (e.g., dashboard passed it), keep form in sync with that too.
  useEffect(() => {
    if (!initialData) return;
    setForm({
      firstName: initialData?.firstName ?? '',
      lastName: initialData?.lastName ?? '',
      username: initialData?.username ?? '',
      email: initialData?.email ?? '',
      password: '',
      confirmPassword: '',
    });
    setImageUrl(initialData?.image || '');
    setImagePreview(initialData?.image || null);
    setImageFile(null);
    setImageToDelete(null);

    setChangePassword(Boolean(forceChangePassword));
    setChangeUsername(Boolean(forceChangeUsername));
    setNewPassword('');
    setConfirmNewPassword('');
    setNewUsername('');
    setConfirmNewUsername('');
    setError('');
    setSuccess('');
    setUsernameInvalid(false);
    initialRef.current = initialData;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialData]);

  const handleUsernameBlur = async () => {
    if (!form.username) return;
    try {
      setCheckingUsername(true);
      const url =
        mode === 'edit' && userId
          ? `/api/users/check-exact?userId=${encodeURIComponent(
              userId
            )}&username=${encodeURIComponent(form.username)}`
          : `/api/users/check-available?username=${encodeURIComponent(form.username)}`;
      const res = await fetch(url);
      if (!res.ok) {
        setUsernameInvalid(false);
        setCheckingUsername(false);
        return;
      }
      const data = await res.json().catch(() => null);
      const valid = data && (data.valid === true || data.available === true || data.ok === true);
      setUsernameInvalid(!valid);
    } catch {
      setUsernameInvalid(false);
    } finally {
      setCheckingUsername(false);
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
    setError('');
    setSuccess('');
    if (e.target.name === 'username') setUsernameInvalid(false);
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    if (file) {
      if (imageUrl) setImageToDelete(imageUrl);
      setImageFile(file);
      setImagePreview(URL.createObjectURL(file));
    }
  };

  const handleImageDelete = async () => {
    if (imageUrl) setImageToDelete(imageUrl);
    setImageFile(null);
    setImageUrl('');
    setImagePreview(null);
    if (userId && imageUrl) {
      try {
        await fetch('/api/users/delete-image', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ imageUrl }),
        });
      } catch {
        // ignore
      }
    }
  };

  // Animated dots for submit button
  useEffect(() => {
    let dotInterval: any;
    if (isSubmitting) {
      dotInterval = setInterval(() => {
        setRegisterDot((prev) => (prev + 1) % 3);
      }, 400);
    } else {
      setRegisterDot(0);
    }
    return () => {
      if (dotInterval) clearInterval(dotInterval);
    };
  }, [isSubmitting]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setIsSubmitting(true);

    // --- Validation ---
    if (!isEdit) {
      if (
        !form.firstName ||
        !form.lastName ||
        !form.username ||
        !form.email ||
        !form.password ||
        !form.confirmPassword ||
        (!imageFile && !imagePreview)
      ) {
        setError('All fields and image are required');
        setIsSubmitting(false);
        return;
      }
      if (form.password !== form.confirmPassword) {
        setError('Passwords do not match');
        setIsSubmitting(false);
        return;
      }
    } else {
      if (changeUsername) {
        if (!newUsername || !confirmNewUsername) {
          setError('Please provide the new username and confirmation');
          setIsSubmitting(false);
          return;
        }
        if (newUsername !== confirmNewUsername) {
          setError('New usernames do not match');
          setIsSubmitting(false);
          return;
        }
      }
      if (changePassword) {
        if (!newPassword || !confirmNewPassword) {
          setError('Please provide the new password and confirmation');
          setIsSubmitting(false);
          return;
        }
        if (newPassword !== confirmNewPassword) {
          setError('New passwords do not match');
          setIsSubmitting(false);
          return;
        }
      }
      if (usernameInvalid) {
        setError('The Username appears invalid');
        setIsSubmitting(false);
        return;
      }
      if (!userId && !initialRef.current?.id) {
        setError('Invalid form mode or missing user ID');
        setIsSubmitting(false);
        return;
      }
    }

    try {
      const formData = new FormData();

      if (!isEdit) {
        formData.append('firstName', form.firstName);
        formData.append('lastName', form.lastName);
        formData.append('username', form.username);
        formData.append('email', form.email);
        formData.append('password', form.password);
        if (imageFile) formData.append('image', imageFile);
      } else {
        const orig = initialRef.current ?? {};

        // include original username to help server-side checks
        if (orig.username) formData.append('username', orig.username);
        else if (form.username) formData.append('username', form.username);

        if (form.firstName !== (orig.firstName ?? '')) formData.append('firstName', form.firstName);
        if (form.lastName !== (orig.lastName ?? '')) formData.append('lastName', form.lastName);
        if (form.email !== (orig.email ?? '')) formData.append('email', form.email);

        if (changeUsername) formData.append('newUsername', newUsername);

        if (changePassword) formData.append('password', newPassword);
        else if (form.password) formData.append('password', form.password);

        if (imageFile) formData.append('image', imageFile);
        if (imageToDelete) formData.append('oldImageUrl', imageToDelete);
      }

      let res;
      if (isEdit) {
        const idToUse = userId ?? initialRef.current?.id;
        res = await fetch(`/api/users/${encodeURIComponent(String(idToUse))}`, {
          method: 'PATCH',
          body: formData,
        });
      } else {
        res = await fetch('/api/users', {
          method: 'POST',
          body: formData,
        });
      }

      const data = await res.json().catch(() => null);
      if (!res.ok) throw new Error((data && (data.error || data.message)) || 'Operation failed');

      setSuccess(isEdit ? 'User updated!' : 'User created!');

      if (!isEdit) {
        const cameFrom = getCameFrom();
        sessionStorage.removeItem('came-from');
        setTimeout(() => {
          router.push(cameFrom);
        }, 800);
      } else {
        if (data?.user) {
          setImageUrl(data.user.image || '');
          setImagePreview(data.user.image || null);
          setForm((f) => ({ ...f, username: data.user.username || f.username }));
          initialRef.current = { ...(initialRef.current || {}), ...data.user };
        }
        if (onSuccess) onSuccess();
      }
    } catch (err: any) {
      setError(err?.message || 'Something went wrong');
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => {
    if (onCancel) onCancel();
    else router.back();
  };

  const handleDelete = async () => {
    const idToUse = userId ?? initialRef.current?.id;
    if (!idToUse) return;
    if (!window.confirm('Are you sure you want to delete this user? This action cannot be undone.'))
      return;
    setDeleting(true);
    try {
      const res = await fetch(`/api/users/${encodeURIComponent(String(idToUse))}`, {
        method: 'DELETE',
      });
      if (!res.ok) {
        const data = await res.json().catch(() => null);
        throw new Error(data?.error || 'Failed to delete user');
      }
      if (onDelete) onDelete(String(idToUse));
      if (onSuccess) onSuccess();
      router.refresh();
      router.push('/admin/dashboard');
    } catch (err: any) {
      setError(err.message || 'Something went wrong');
    } finally {
      setDeleting(false);
    }
  };

  return (
    <div className="flex max-w-lg items-center justify-center mx-auto">
      {loadingUser ? (
        <div className="p-6 text-center">Loading…</div>
      ) : (
        <form onSubmit={handleSubmit} className="space-y-6 w-full">
          {/* User Image */}
          <div className="flex flex-col items-center">
            {imagePreview ? (
              <div className="relative flex flex-col items-center">
                <img
                  src={imagePreview}
                  alt="User"
                  className="w-24 h-24 rounded-full object-cover border mx-auto"
                />
                <button
                  type="button"
                  className="absolute top-0 right-0 bg-red-500 text-white rounded-full w-6 h-6 flex items-center justify-center -mt-2 -mr-2"
                  onClick={handleImageDelete}
                  title="Delete Image"
                >
                  ×
                </button>
              </div>
            ) : (
              <div className="w-24 h-24 rounded-full border flex items-center justify-center mb-2 text-gray-400">
                No Image
              </div>
            )}
            <input
              type="file"
              accept="image/*"
              onChange={handleImageChange}
              className="mt-2 mb-2 block"
            />
          </div>

          {/* Name row */}
          <div className="flex gap-4">
            <input
              name="firstName"
              placeholder="First Name"
              value={form.firstName}
              onChange={handleChange}
              className="border p-2 rounded w-1/2"
            />
            <input
              name="lastName"
              placeholder="Last Name"
              value={form.lastName}
              onChange={handleChange}
              className="border p-2 rounded w-1/2"
            />
          </div>

          {/* Username and Email */}
          <div className="flex gap-4">
            <input
              name="username"
              placeholder="Username"
              value={form.username}
              onChange={handleChange}
              onBlur={mode === 'edit' ? handleUsernameBlur : undefined}
              className={`border p-2 rounded w-1/2 ${usernameInvalid ? 'border-red-500' : ''}`}
              autoComplete="off"
              disabled={changeUsername}
            />
            <input
              name="email"
              type="email"
              placeholder="Email"
              value={form.email}
              onChange={handleChange}
              className="border p-2 rounded w-1/2"
              autoComplete="off"
            />
          </div>
          {checkingUsername && mode === 'edit' && (
            <span className="text-xs block">Checking username…</span>
          )}
          {usernameInvalid && mode === 'edit' && (
            <span className="text-xs text-red-500 block">The Username appears invalid.</span>
          )}

          {/* Optional: change username section (edit mode) */}
          {isEdit && (
            <div className="border rounded p-3 bg-gray-50 dark:bg-gray-800">
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={changeUsername}
                  onChange={() => {
                    setChangeUsername(!changeUsername);
                    setNewUsername('');
                    setConfirmNewUsername('');
                    setError('');
                  }}
                />
                <span className="text-sm font-medium">Change username</span>
              </label>

              {changeUsername && (
                <div className="mt-3 space-y-2">
                  <input
                    placeholder="New username"
                    value={newUsername}
                    onChange={(e) => setNewUsername(e.target.value)}
                    className="border p-2 rounded w-full"
                    autoComplete="off"
                  />
                  <input
                    placeholder="Confirm new username"
                    value={confirmNewUsername}
                    onChange={(e) => setConfirmNewUsername(e.target.value)}
                    className="border p-2 rounded w-full"
                    autoComplete="off"
                  />
                  <div className="text-xs text-muted-foreground">
                    If you change username, you'll be updating the account handle.
                  </div>
                </div>
              )}
            </div>
          )}

          {/* Password fields */}
          {!isEdit ? (
            <div className="flex gap-4">
              <input
                type="password"
                name="password"
                placeholder="Password"
                onChange={handleChange}
                value={form.password}
                className="border p-2 rounded w-1/2"
                autoComplete="new-password"
              />
              <input
                type="password"
                name="confirmPassword"
                placeholder="Confirm Password"
                onChange={handleChange}
                value={form.confirmPassword}
                className="border p-2 rounded w-1/2"
                autoComplete="new-password"
              />
            </div>
          ) : (
            <div className="border rounded p-3 bg-gray-50 dark:bg-gray-800">
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  checked={changePassword}
                  onChange={() => {
                    setChangePassword(!changePassword);
                    setNewPassword('');
                    setConfirmNewPassword('');
                    setError('');
                  }}
                />
                <span className="text-sm font-medium">Change password</span>
              </label>

              {changePassword && (
                <div className="mt-3 flex gap-4">
                  <input
                    type="password"
                    placeholder="New password"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    className="border p-2 rounded w-1/2"
                    autoComplete="new-password"
                  />
                  <input
                    type="password"
                    placeholder="Confirm new password"
                    value={confirmNewPassword}
                    onChange={(e) => setConfirmNewPassword(e.target.value)}
                    className="border p-2 rounded w-1/2"
                    autoComplete="new-password"
                  />
                </div>
              )}
              {!changePassword && (
                <div className="text-xs text-muted-foreground mt-2">
                  Leave password blank to keep existing password.
                </div>
              )}
            </div>
          )}

          {error && <p className="text-red-500 text-sm">{error}</p>}
          {success && <p className="text-green-600 text-sm">{success}</p>}

          <div className="flex gap-3">
            <button
              type="submit"
              className="w-1/2 bg-green-600 text-white py-2 rounded flex items-center justify-center"
              disabled={
                isSubmitting ||
                checkingUsername ||
                usernameInvalid ||
                (!isEdit && form.password !== form.confirmPassword) ||
                (isEdit && changePassword && newPassword !== confirmNewPassword) ||
                (isEdit && changeUsername && newUsername !== confirmNewUsername)
              }
            >
              {isEdit ? 'Edit' : 'Register'}
              {isSubmitting && (
                <span className="ml-2 flex">
                  {[0, 1, 2].map((i) => (
                    <span
                      key={i}
                      className={`transition-all w-1.5 h-1.5 rounded-full mx-0.5 inline-block ${
                        registerDot === i ? 'bg-white' : 'bg-green-900 opacity-40'
                      }`}
                    ></span>
                  ))}
                </span>
              )}
            </button>

            <button
              type="button"
              onClick={handleCancel}
              className="w-1/2 bg-black text-white py-2 rounded"
              disabled={deleting}
            >
              Cancel
            </button>
          </div>

          {isEdit && (userId ?? initialRef.current?.id) && (
            <div className="flex flex-col gap-2">
              <button
                type="button"
                onClick={handleDelete}
                className="w-full bg-red-600 text-white py-2 rounded mt-2"
                disabled={deleting}
              >
                {deleting ? 'Deleting...' : 'Delete User'}
              </button>
            </div>
          )}
        </form>
      )}
    </div>
  );
}
